@import "https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap";
@import "https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400;700&display=swap";
.metro-songlist {
  width: clamp(260px,100%,500px);
}
.metro-songlist .details {
  display: flex;
  justify-content: space-between;
  align-items: center;
  white-space: nowrap;
  width: .1vw;
}
.details.genres {
  white-space: nowrap;
  border: 1px solid red;
}
.metro-songlist .index {
  width: .2%;
}
.songlist-nav-ctn {
  display: flex;
  justify-content: center;
}
.songlist-nav-btn {
  cursor: pointer;
}
.songlist-nav-btn:hover {
  color: #b8d5b8;
}
.songList-spinner {
  width: 100%;
}