@import "https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap";
@import "https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400;700&display=swap";
.metronome-ctn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: clamp(400px,80svw,2000px);
  margin: 5em auto;
}
.metronome {
  min-width: 400px;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.5em;
  background-color: #000;
  border-radius: 10px;
  box-shadow: teal 15px 15px 100px;
  transition: 150ms all cubic-bezier(0.49, 0.03, 0.61, 1.93);
}
.metronome .header img {
  color: #1edfa5;
  height: 100px;
  object-fit: cover;
  width: auto;
  aspect-ratio: 1/1;
  margin-bottom: 1em;
}
.metro-display {
  width: 100%;
  display: grid;
  grid-template-columns: 20% 1fr 20%;
  border-radius: 16px;
  justify-content: center;
  align-items: center;
  background-color: teal;
  height: 65px;
}
.metro-display>.metro-btn {
  height: 100%;
  text-align: center;
  background-color: #000;
  border: 1px solid teal;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5em;
  color: #1edfa5;
  transition: 150ms ease-in;
}
.metro-display>.metro-btn:hover {
  box-shadow: teal 5px 5px 50px;
  background-color: #1edfa5;
  color: #000;
}
.metro-display>.bpm {
  display: flex;
  border: none;
  font-size: 1.5em;
  width: 100%;
}
.metro-btn.decrement,.metro-btn.increment {
  margin-top: -5px;
  transition: 100ms all ease-in-out;
}
.metro-btn.decrement {
  border-radius: 16px 0 0 16px;
}
.metro-btn.increment {
  border-radius: 0px 16px 16px 0px;
}
.metro-btn.increment:hover,.metro-btn.decrement:hover {
  background-color: #1edfa5;
  color: #000;
}
.metro-btn.decrement:active,.metro-btn.increment:active {
  margin-top: 0px;
}
