  box-shadow: 1px 1px 1px #000,0px 0px 1px #0d0d0d;
}
input[type=range]::-ms-fill-upper {
  background: #3071a9;
  border: .2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000,0px 0px 1px #0d0d0d;
