}
.noSelect {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.noSelect:focus {
  outline: none !important;
}
.metro-controls {
  display: flex;
  flex-direction: column;
  height: auto;
  width: 100%;
  align-items: center;
  position: relative;
  padding: 1em 0 0 0;
}
.metro-btn {
  cursor: pointer;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.play {
  box-sizing: content-box;
  z-index: 10;
  border: .125em solid #1edfa5;
  height: 5em;
  width: 5em;
  background-color: #000;
  border-radius: 250px;
  position: relative;
  transition: ease-in 150ms;
  margin-bottom: 2em;
}
.play:hover {
  box-shadow: teal 15px 15px 100px;
}
.play:hover:active {
  box-shadow: none;
  color: #000;
}
.play:after {
  content: "";
  height: 50%;
  width: 45%;
  position: absolute;
  top: 25%;
  left: 35%;
  background-image: url("/play.svg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.metro-btn-tap {
  height: 5em;
  width: 5em;
  border: teal 1px solid;
  background-color: #000;
  border-radius: 25px;
  position: absolute;
  right: 0%;
  z-index: 9;
  color: #1edfa5;
  transition: ease-in 150ms;
  cursor: pointer;
}
.metro-btn-tap .outer {
  content: "";
  width: 30px;
  height: 2em;
  position: relative;
  border: 5px solid #1edfa5;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.metro-btn-tap .inner {
  content: "";
  width: 15px;
  height: 15px;
  position: absolute;
  top: 50%;
  right: 50%;
  border: 5px solid #1edfa5;
  border-radius: 50%;
  transform: translate(50%, -50%);
  transition: ease-in 250ms transform;
}
.metro-btn-tap:hover {
  box-shadow: teal 15px 15px 100px;
}
.metro-btn-tap:hover:active {
  box-shadow: none;
  color: #000;
}
.metro-btn-generate {
  height: 5em;
  width: 5em;
  border: none;
  background-color: rgba(0,0,0,0);
  border-radius: 25px;
  border: 1px solid teal;
  position: absolute;
  left: 0%;
  z-index: 9;
  color: #1edfa5;
  transition: ease-in 150ms;
  cursor: pointer;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.metro-btn-generate:hover {
  opacity: 100%;
  box-shadow: teal 15px 15px 100px;
}
.pause {
  box-sizing: content-box;
  z-index: 10;
  border: .125em solid #1edfa5;
  height: 5em;
  width: 5em;
  background-color: rgba(0,0,0,0);
  border-radius: 250px;
  position: relative;
  color: #1edfa5;
  margin-bottom: 2em;
}
.pause:after {
  content: "";
  background-image: url("/pause.svg");
  height: 2.5em;
  width: 2.5em;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-size: cover;
  background-position: center;
}
.pause:hover {
  box-shadow: teal 15px 15px 100px;
}
.pause:hover:active {
  box-shadow: none;
  color: #000;
}
.metro-dropdown-header {
  padding: .75em;
  background-color: teal;
  border-radius: 26px;
  width: 100%;
  transition: ease-in 150ms;
  cursor: pointer;
  text-transform: uppercase;
  text-align: center;
  font-weight: bolder;
  display: flex;
}
.metro-dropdown-header:hover {
  background-color: var(--primary);
}
.metro-dropdown {
  transition: cubic-bezier(1, 0, 0, 1) 250ms;
  position: absolute;
  height: 20%;
  width: 100%;
}
.metro-dropdown.open {
  bottom: -97%;
  right: 1%;
  height: 100%;
  width: 100%;
  transition: ease-in 100ms;
  padding: .5em;
}
.metro-dropdown.closed {
  transition: ease-in 100ms;
  transform: translateY(-45%);
}
.metro-dropdown-item {
  transition: ease-in 150ms;
  padding: 1em;
  background-color: teal;
  border-radius: 26px;
  width: 100%;
  margin: .5em 0;
}
.metro-dropdown-item:hover {
  background-color: var(--primary);
}
@keyframes pulse {
  0% {
    box-shadow: teal 15px 15px 100px;
  }
  100% {
    box-shadow: teal 15px 15px 0px;
  }
}
@media (max-width: 900px) {
  .metronome-ctn {
    flex-direction: column;
    align-items: center;
  }
}